.schedule {
  padding: 2% 5%;
}

.match {
  border-radius: 10px;
  background: #fff;
  box-shadow: 0 5px 10px rgb(0 57 111 / 15%);
  border-radius: 10px;
  padding: 20px;
  margin: 3% 0%;
  text-transform: uppercase;
}

.time {
  color: #18184a;
  font-style: italic;
}

.team-name-1 {
  font-size: 1.3em;
  margin-left: 2%;
  text-transform: uppercase;
  text-align: center;
}

.team-name-2 {
  text-align: center;
  font-size: 1.3em;
  margin-right: 2%;
  text-transform: uppercase;
}

.date {
  color: red;
}

.teams {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.team-logo {
  max-width: 90px;
}

.team-1,
.team-2 {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 48%;
}

.team-1 {
  justify-content: flex-start;
}
.team-2 {
  justify-content: flex-end;
}
.vs {
  color: gray;
}
@media (max-width: 640px) {
  .team-2 {
    display: flex;
    flex-direction: column-reverse;
    margin: 0%;
  }
  .team-1 {
    display: flex;
    flex-direction: column;
    margin: 0%;
  }
}

.schedule-table {
  width: 100%;
  border-collapse: collapse;
}

.schedule-table th,
.schedule-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.schedule-table th {
  background-color: #f2f2f2;
  color: black;
}

.schedule-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.schedule-table tr:hover {
  background-color: #ddd;
}
